
/**
 * 
 */
cc.Class({
    extends: cc.Component,
    properties: {
        uid:66,
        nickname:"11",//
        headimg:"http://192.168.1.188/Login/functionServlet",//
        roomId:null,//
        join:null,//
        my:null,//
        createJ:null,//
        notice:"",//
        diamond:0,
        ip:"192.168.1.1"
    },
    ctor(){
        wsft.message.addMessage(wsft.protocol.RECEIVE_LOGIN,this.socketLogin.bind(this))
        wsft.message.addMessage(wsft.protocol.PLAYER_CAST,this.playerCast.bind(this))//
    },
    socketLogin:function(data){
        console.log("date==",data)
        if(data.code == 1){
            this.nickname = data.nickname
            this.headimg=data.headimg
            this.roomId=data.roomId
            this.notice=data.notice
            this.join=data.join
            this.my=data.my
            this.createJ=data.createJ
            if(this.roomId){
                //
                wsft.sendDataSocket.OfflineReconnection()
                wsft.loading.show("...") 
                // wsft.uiFuncs.showTip(":"+this.roomId,30)
                // wsft.loading.hide()
            }else{
                this.jumpScene()
            } 
        }else{
            wsft.uiFuncs.showTip("",30)
            wsft.loading.hide() 
        }
    },
    playerCast:function(data){
        console.log("date==",data)
        this.diamond = data.diamond
        
    },
    jumpScene:function(){
        wsft.loading.show("...") 
        cc.director.loadScene('MainScene')
    }

});


