window.wsft = window.wsft || {}

/**
 * bindFuncList
 * [
 * 'event1':[func1,func2],
 * 'event2',[func3,func4]
 * ]
 */
var bindFuncList = [];// 
/**
 * 
 * emitList
 * [
 * 'event1':[args1,args2],
 * 'event2':[args3,args4];
 * ]
 */
var emitList = [];

var message = {
/**
 * 
 * @param {} key 
 * @param {} cbFunc 
 */
// 
    addMessage:function(key,cbFunc){
        if (bindFuncList[key]){
            bindFuncList[key].push(cbFunc);
        }else {
            var ary = new Array();
            ary.push(cbFunc);
            bindFuncList[key] = ary;
        }
    },
/**
 * 
 * @param {} key 
 * @param {} args 
 */
// emit
    sendMessage:function(key,args){
        var ary = bindFuncList[key];
        if(ary){// 
            for (var i in ary) {
                if (ary.hasOwnProperty(i)) {
                    try {
                        ary[i].call(this,args);
                    } catch (error) {
                        
                    }
                }
            }
        }else {// emit
            if (emitList[key]){
                emitList[key].push(args);
            }else {
                var ary = new Array();
                ary.push(args);
                emitList[key] = ary;
            }
        }
    },
// sendAllMessage
    sendNoMessage:function(){
        for (var key in emitList) {
            if (emitList.hasOwnProperty(key)) {
                var emitAry = emitList[key];
                for (var j in emitAry) {
                    if (emitAry.hasOwnProperty(j)) {
                        var args = emitAry[j];// 
                        var ary = bindFuncList[key];// 
                        // 
                        for (var iterator in ary) {
                            if (ary.hasOwnProperty(iterator)) {
                                try {
                                    ary[iterator].call(this,args);
                                } catch (error) {

                                }
                            }
                        }
                        
                    }
                }
            }
        }
        emitList = [];
    },

// 
    removeAllMessage:function(){
        bindFuncList = [];
    },
//   
    removeKeyMessage:function(_key){
        if(bindFuncList[_key]){
            bindFuncList[_key] = null;
        }
    }

}

wsft.message = module.exports = message;

