    receiveOfflineReconnection:function(data){//()
        console.log("receiveOfflineReconnection==",data) 
        if (data.code == 1){
            this.roomId = data.roomId;//
            this.rule = data.playType;//
            this.playerNum = data.playType.num;//
            this.seats = data.users;
            this.setRuleLocalStorage(data.playType)
            for(var i = 0;i< data.users.length;++i){
                if(wsft.userData.uid == data.users[i].uid ){
                    this.mySeatIndex = data.users[i].seetIndex
                }
            }
            if (data.start == false){
                this.gameState = "NO"
                this.jumpGameScene()
            }else{
                this.gameState = "YES"
                this.reset()
                //2,5,8,11,14
                if(data.my){
                    this.cardNum = data.my.leftPaiNum;
                    this.seats[this.mySeatIndex - 1].handCard =wsft.funcs.deepCopy(data.my.pais);
                }
                this.bankerSeatIndex = data.bankerSeetIndex
                // type = 1;//1 2 3 4 5
                for(var i in data.usersRound){
                    var seatData = this.seats[data.usersRound[i].seetIndex - 1];
                    // this.seats[data.usersRound[i].seetIndex - 1].outOldCard = data.usersRound[i].mj
                    for(var j = 0;j < data.usersRound[i].mj.length ;++j){//
                        seatData.outOldCard.push(data.usersRound[i].mj[j])
                    }
                    var gpc = data.usersRound[i].gpc
                    for(var g = 0;g < gpc.length;++g){// 
                        var type = gpc[g].type
                        // console.log("typetype==",type)
                        if(type == 1){
                            seatData.anGangs.push(gpc[g].mj);
                        }else if(type == 2){
                            seatData.mingGangs.push(gpc[g].mj);
                        }else if(type == 3){
                            seatData.jieGangs.push(gpc[g].mj);
                        }else if(type == 4){
                            seatData.pengs.push(gpc[g].mj);
                        }
                    }
                }
                this.jumpGameScene()
                wsft.message.sendMessage(wsft.protocol.RECEIVEOFFLINERECONNECTION,data);
            }
        }else{

        }
    },
    pushRoundResult:function(data){// 825
        console.log("pushRoundResult==",data)
        for(var i in data.rs){
            var seatData = this.seats[data.rs[i].seetIndex - 1];
            seatData.handCard =  wsft.funcs.deepCopy(data.rs[i].mj)
            if(data.reconnect){
                var gpc = data.rs[i].gpc
                for(var g = 0;g < gpc.length;++g){// 
                    var type = gpc[g].type
                    // console.log("typetype==",type)
                    if(type == 1){
                        seatData.anGangs.push(gpc[g].mj);
                    }else if(type == 2){
                        seatData.mingGangs.push(gpc[g].mj);
                    }else if(type == 3){
                        seatData.jieGangs.push(gpc[g].mj);
                    }else if(type == 4){
                        seatData.pengs.push(gpc[g].mj);
                    }
                } 
            }else{
            }
            
        }
        wsft.message.sendMessage(wsft.protocol.PUSHROUNDRESULT,data);
    },
    gameOver:function(data){// 826
        console.log("gameOver==",data)
        
        wsft.message.sendMessage(wsft.protocol.PUSHGAMEOVER,data);
        // this.jumpMainScene()
    },
    setInitCardNum:function(){//
        if(this.playerNum  == 4){
            this.cardNum = 84;
        }else if(this.playerNum  == 3){
            this.cardNum = 97;
        }else if(this.playerNum  == 2){
            this.cardNum = 110;
        }
    },
    //()
    setRuleLocalStorage:function(_rule){
        var ruleName = ["ChiFengMJ","DouDiZhu"]
        var _chekedTable = [];
        if(_rule.bankerMode == 1){
            _chekedTable.push(0)
        };
        if(_rule.bankerMode == 2){
            _chekedTable.push(1)
        };
        if(_rule.bankerMode == 3){
            _chekedTable.push(2)
        };
        if(_rule.zhifu == true){
            _chekedTable.push(3)
        }else{
            _chekedTable.push(4)
        }
        if(_rule.num == 2){
            _chekedTable.push(5)
        }else if(_rule.num == 3){
            _chekedTable.push(6)
        }else if(_rule.num == 4){
            _chekedTable.push(7)
        }
        if(_rule.qiDui){//""
            _chekedTable.push(8)
        }
        if(_rule.piaohu){//""
            _chekedTable.push(9)
        }
        if(_rule.shisanyao){//""
            _chekedTable.push(10)
        }
        if(_rule.gangshangpao){//""
            _chekedTable.push(11)
        }
        if(_rule.qingYiSe){//""
            _chekedTable.push(12)
        }
        if(_rule.diangang){//""
            _chekedTable.push(13)
        }
        var ruleData = {
            name: ruleName[0],
            chekedTable: _chekedTable,
        };
        cc.sys.localStorage.setItem('ruleData', JSON.stringify(ruleData));
    },
    //
    getRule:function(){
        var _str = ""
        if(this.rule){
            if(this.rule.bankerMode == 1){
                if(this.rule.num == 4){
                    _str +="1  "
                }
                if(this.rule.num == 3){
                    _str +="5  "
                }
                if(this.rule.num == 2){
                    _str +="6  "
                }
            }
            if(this.rule.bankerMode == 2){
                if(this.rule.num == 4){
                    _str +="2  "
                }
                if(this.rule.num == 3){
                    _str +="10  "
                }
                if(this.rule.num == 2){
                    _str +="12  "
                }
            }
            if(this.rule.bankerMode == 3){
                if(this.rule.num == 4){
                    _str +="4  "
                }
                if(this.rule.num == 3){
                    _str +="20  "
                }
                if(this.rule.num == 2){
                    _str +="26  "
                }
            }
            if(this.rule.zhifu == true){
                _str += "  "
            }else{
                _str += "AA  "
            }
            _str += this.rule.num + " "
            if(this.rule.qiDui){
                _str += "  "
            }
            if(this.rule.piaohu){
                _str += "  "
            }
            if(this.rule.shisanyao){
                _str += "  "
            }
            if(this.rule.gangshangpao){
                _str += "  "
            }
            if(this.rule.qingYiSe){
                _str += "  "
            }
            if(this.rule.diangang){
                _str += "  "
            }
        }else{
            return
        }
        return _str  
    },
    //
    getLocalIndex:function(index){
        var _index = (index - this.mySeatIndex + 4) % 4;
        if(this.playerNum  == 3){
            if(this.mySeatIndex == 1){
                if(_index == 2){
                    _index = 3
                }
            }
            if(this.mySeatIndex == 2){
                if(_index == 2){
                    _index = 1
                }
            }
            if(this.mySeatIndex == 3){
                if(_index == 2){
                    _index = 1
                }
            }

        }else if(this.playerNum  == 2){
            if(this.mySeatIndex == 1){
                if(_index == 1){
                    _index = 2
                }
            }
            if(this.mySeatIndex == 2){
                if(_index == 3){
                    _index = 2
                }
            }
        }
        return _index;
    },
    //
    getSeatId:function(otherId){
        var seatId = 0
        var myId = this.mySeatIndex
        if(myId == otherId){
            seatId = 1
            return seatId
        }else{
            if(otherId > myId){
                var seatId = otherId + myId - 1
                if(seatId > 4){
                    seatId-=4;
                }
                return seatId
            }else{
                var seatId = myId - otherId +1
                return seatId
            }
        }
    },
