cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var canvas = this.node.getComponent(cc.Canvas);
        if(cc.sys.isBrowser){ //
            var designResolutionBI = canvas.designResolution.width/canvas.designResolution.height
            var visibleRectSize = cc.visibleRect.width/cc.visibleRect.height
            console.log("cc.visibleRect.width==",cc.visibleRect.width)
            console.log("cc.visibleRect.height==",cc.visibleRect.height)
            if ( designResolutionBI >= visibleRectSize) {
                canvas.fitWidth = false;
                canvas.fitHeight = true;
                // canvas.designResolution.width = cc.visibleRect.width
                // cc.log("");
            } else {
                canvas.fitWidth = true;
                canvas.fitHeight = false;
                // canvas.designResolution.height = cc.visibleRect.height
                // cc.log("");
            }
        }else{

        }
    },

    start () {

    },

    // update (dt) {},
});
